html {
	text-rendering: optimizeLegibility;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	width: 100%;
	overflow-x: hidden;
	margin: 0;
	font-size: 50%;

	@screen md {
		font-size: 56.25%;
	}

	@screen 2xl {
		font-size: 62.5%;
	}
}

body {
	@apply relative font-body font-normal w-full overflow-hidden m-0;
	font-size: 1.8rem;
	line-height: 1.72;

	@screen lg {
		@apply overflow-hidden;
	}
}

p {
	+ p {
		margin-top: 1.5em;
	}

	> a {
		@apply text-blue-team;

		&:hover {
			@apply opacity-60;
		}
	}
}

a {
	transition: 0.3s color, 0.3s opacity;
	cursor: pointer;
}

b,
strong {
	@apply font-bold;
}

h1,
.h1 {
	font-size: 4rem;
	line-height: 1.33;
}

h2,
.h2 {
	font-size: 3rem;
	line-height: 1.33;
}

h3,
.h3 {
	font-size: 2.2rem;
	@apply uppercase;
}

section {
	position: relative;
	z-index: 1;
	overflow: hidden;
}

.s-padding {
	padding: max(30px, 6vw) 0;
}
.s-padding-half {
	padding: max(30px, 3vw) 0;
}
.s-margin {
	margin: max(30px, 6vw) 0;
}
.s-margin-half {
	margin: max(30px, 3vw) 0;
}

.s-padding-top {
	padding-top: max(30px, 4vw);
}
.s-padding-bottom {
	padding-bottom: max(30px, 4vw);
}
.s-margin-top {
	margin-top: max(30px, 3vw);
}
.s-margin-bottom {
	margin-bottom: max(30px, 3vw);
}

.container {
	width: calc(100% - 1.5rem);
	max-width: none;

	@screen md {
		width: calc(100% - 5rem);
	}
}

.row {
	@apply flex flex-wrap;
}