.heroes-grid {

    label {
        font-size: 80%;
    }

    input {
        line-height: 1.4;
        padding: 1rem 1.4rem;

        &:focus,
        &:active {
            outline: none;
        }
        &.checked {
            outline: 1px solid #fff;
            outline-offset: 1px;
        }

        + .icon svg {
            fill: #fff;
            width: 50%;
            height: 50%;
            opacity: 0.6;
        }
    }

    select {
        &:focus,
        &:active {
            outline: none;
        }
        &.checked {
            outline: 1px solid #fff;
            outline-offset: 1px;
        }
    }

}

.hero {

    .inner {
        position: relative;

        &:before {
            content: '';
            position: absolute;
            z-index: -1;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(177,177,177,0.4);
            @apply rounded-xl;
            //transition: 0.2s top, 0.2s left, 0.2s width, 0.2s height;
        }
    }

    @screen lg {
        &:hover {
            .inner {
                &:before {
                    top: -0.4rem;
                    left: -0.4rem;
                    width: calc(100% + 0.8rem);
                    height: calc(100% + 0.8rem);
                }
            }
        }
    }

    &[data-pinned=true] {
        .inner {
            &:before {
                top: -0.4rem;
                left: -0.4rem;
                width: calc(100% + 0.8rem);
                height: calc(100% + 0.8rem);
                background: #a0eec0;
            }
        }
    }

    hr {
        color: #b1b1b1;
    }

    .name {
        img {
            border-radius: 0.5rem 0.5rem 0 0;
        }

        h3 {
            line-height: 1.25;
        }

        span {
            display: block;
            font-size: 75%;
            font-weight: normal;
            text-transform: capitalize;
            margin-top: 0.5em;
        }

        .disk {
            width: 3.3rem;
            height: 3.3rem;
            clip-path: polygon(0 25%, 50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%);
            background: #b1b1b1;
            transform: translate(25%, -25%);

            p {
                z-index: 1;
                font-size: 68%;
                font-weight: normal;
            }

            &:after {
                content: '';
                position: absolute;
                top: 1px;
                left: 1px;
                width: calc(100% - 2px);
                height: calc(100% - 2px);
                pointer-events: none;
                clip-path: polygon(0 25%, 50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%);
                z-index: 0;
                @apply bg-blue;
            }
        }
    }

    .toggles {

        span {
            position: relative;
            padding: 0.75rem 1rem;
            @apply bg-blue cursor-pointer;
            transition: 0.3s all;

            &:first-child:last-child {
                border-radius: 0.5rem;
            }

            &:first-child:not(:last-child) {
                border-radius: 0.5rem 0 0 0.5rem;

                &:after {
                    content: '';
                    position: absolute;
                    top: 0.5rem;
                    right: 0;
                    width: 1px;
                    height: calc(100% - 1rem);
                    background: #b1b1b1;
                }
            }

            &:last-child:not(:first-child) {
                border-radius: 0 0.5rem 0 0;
            }

            &:not(.active) {
                background: rgba(9,28,37,0.4);
                color: rgba(255,255,255,0.4);

                &:hover {
                    @apply bg-blue text-white;
                }
            }
        }
    }

    .details {
        pointer-events: none;
        user-select: none;

        p {
            margin: 0;
        }
    }

    &[data-team="yellow"] {
        .details {
            > div:first-child {
                @apply text-blue;
            }
        }
    }

    .mods {
        pointer-events: none;
        user-select: none;

        > div {
            display: flex;
            align-items: center;
            justify-content: space-between;
            box-shadow: 
            1px 0 0 0 #b1b1b1,
            0 1px 0 0 #b1b1b1,
            1px 1px 0 0 #b1b1b1,   /* Just to fix the corner */
            1px 0 0 0 #b1b1b1 inset,
            0 1px 0 0 #b1b1b1 inset;
        }

        svg {
            fill: currentColor;
            opacity: 0.5;
        }

        &.set-2 {
            opacity: 0;
            visibility: hidden;
            transition: 0.3s all;

            &.active {
                opacity: 1;
                visibility: visible;
            }
        }
    }

    svg {
        fill: #fff;
        height: 1em;
        width: 1em;
        margin: 0 1rem;
    }

}