.mouse-scroll:before {
	position: absolute;
	left: 50%;
}

.mouse-scroll {
	position: relative;
	border: 1px solid #fff;
	border-radius: 2.5rem;
	width: 1.8rem;
	height: 3rem;

	&:before {
		content: '';
		width: 0.4rem;
		height: 0.4rem;
		background: #fff;
		margin-left: -0.2rem;
		top: 0.4rem;
		border-radius: 0.3rem;
		animation: scroll-dot 1.7s infinite;
	}
}

@keyframes scroll-dot {
	from {
		opacity: 1;
	}

	to {
		opacity: 0;
		transform: translateY(2rem);
	}
}
