::selection {
	@apply bg-white text-blue;
}

::-webkit-scrollbar {
	width: 8px;
}

::-webkit-scrollbar-thumb {
	@apply bg-white;
	border-radius: 0.8rem;
	transition: 0.3s background-color;
}

::-webkit-scrollbar-track {
	border-left: 3px solid;
	@apply bg-blue border-blue;
}

figure {
	img,
    video {
		@include img-cover;
	}
}