//Placeholder Color
@mixin placeholder($color, $opacity) {
	&::-webkit-input-placeholder {
		/* Chrome/Opera/Safari */
		color: $color;
		opacity: $opacity;
	}

	&::-moz-placeholder {
		/* Firefox 19+ */
		color: $color;
		opacity: $opacity;
	}

	&:-ms-input-placeholder {
		/* IE 10+ */
		color: $color;
		opacity: $opacity;
	}

	&:-moz-placeholder {
		/* Firefox 18- */
		color: $color;
		opacity: $opacity;
	}

	&:focus::-webkit-input-placeholder {
		/* Chrome/Opera/Safari */
		color: transparent;
	}

	&:focus::-moz-placeholder {
		/* Firefox 19+ */
		color: transparent;
	}

	&:focus:-ms-input-placeholder {
		/* IE 10+ */
		color: transparent;
	}

	&:focus:-moz-placeholder {
		/* Firefox 18- */
		color: transparent;
	}
}

@mixin img-cover {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

@mixin pseudo-cover {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}